import {
	api
} from '../../api/api.js'

import Operation from './child/infoOperation.vue'
import InfoPanel from './child/infoPanel.vue'
import InfoPanelTwo from './child/InfoPanelTwo.vue'
import InfoPanelThree from './child/InfoPanelThree.vue'
import InfoMask from './child/InfoMask.vue'
import Title from './child/InfoTitle.vue'
export default {
	data() {
		return {
			windowWidth: getApp().globalData.windowWidth,
			windowHeight: getApp().globalData.windowHeight,
			top: getApp().globalData.top,
			session_id: getApp().globalData.session_id,
			uid: getApp().globalData.uid,
			list: ['静态账户', '动态账户', '商城积分'],
			active: 0,
			maskShow: false,
			allList: {},
			index:0
		}
	},
	onLoad: () => {
		console.log("************");
	},
	onShow() {
		this.getAssets();
		uni.$on('update', (data) => {
			console.log('监听到事件来自 update ，携带参数 msg 为：' +data.num);
			this.index = data.num;
			uni.hideTabBar();
			this.maskShow = !this.maskShow;
		})
	},
	methods: {
		activeIndexChange(index) {
			this.active = index;
		},
		coinCharging() {
			uni.navigateTo({
				url: "../CoinCharging/CoinCharging"
			})
		},
		collectCoin() {
			uni.navigateTo({
				url: '../CollectCoin/CollectCoin?type=0'
			})
		},
		toTransfer() {
			uni.navigateTo({
				url: '../transfer/transfer'
			})
		},
		tellSuper(tell) {
			if (tell === 'on') {
				/* uni.hideTabBar();
				this.maskShow = !this.maskShow; */
			} else if (tell === 'off') {
				uni.showTabBar();
				this.maskShow = !this.maskShow;
			}
		},
		getAssets() {
			uni.request({
				url: api.assets,
				data: {
					uid: this.uid,
					session_id: this.session_id,
				},
				method: "post",
				header: {
					"Content-Type": "application/x-www-form-urlencoded"
				},
				success: (res) => {
					console.log(res);
					if (res.data.code === "success") {
						this.allList = res.data.result;
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: "none"
						})
					}
				}
			})
		}

	},
	components: {
		Operation,
		InfoPanel,
		InfoPanelTwo,
		InfoPanelThree,
		InfoMask,
		Title
	},
	onLoad: () => {

	}
}
